export default {
  name: "Dropdown",
  data() {
    return {
      selectedItem: 0,
    };
  },
  props: {
    data: {
      type: Array,
    },
    id: {
      type: Number,
    },
    activeItem: {
      type: String,
    },
    onClick: {
      type: Function,
    },
    field_id: {
      type: Number,
    },
    field_type: {
      type: String,
    },
  },
  methods: {
    classHandler(id) {
      return id === this.selectedItem ? "active" : "";
    },

    clickHandler(item, index, id, field_id) {
      this.selectedItem = index;
      this.onClick({
        title: item.title,
        type: item.type,
        fieldType: item.fieldType,
        id: id,
        field_id: field_id,
      });
    },
  },
};
